import { School } from '@/types';

interface SchoolListProps {
  schools: School[];
  selectedSchool: School | null;
  setSelectedSchool: (school: School | null) => void;
}

export default function SchoolList({ 
  schools, 
  selectedSchool, 
  setSelectedSchool 
}: SchoolListProps) {
  return (
    <div className="h-full overflow-auto bg-white rounded-lg shadow">
      <div className="sticky top-0 bg-gray-100 p-4 border-b">
        <h2 className="font-bold text-lg">Schools ({schools.length})</h2>
      </div>
      
      {schools.length === 0 ? (
        <div className="p-4 text-center text-gray-500">
          <p>No schools imported yet.</p>
          <p className="text-sm">Upload a CSV file to get started.</p>
        </div>
      ) : (
        <ul className="divide-y">
          {schools.map((school) => (
            <li 
              key={school.id || `${school.name}-${school.address}`}
              className={`p-4 hover:bg-gray-50 cursor-pointer transition-colors ${
                selectedSchool && selectedSchool.name === school.name && 
                selectedSchool.address === school.address 
                  ? 'bg-blue-50 border-l-4 border-blue-500' 
                  : ''
              }`}
              onClick={() => setSelectedSchool(school)}
            >
              <h3 className="font-medium">{school.name}</h3>
              <p className="text-sm text-gray-600">{school.district}</p>
              <p className="text-sm">{school.address}</p>
              <p className="text-sm">{school.city}, {school.state} {school.zipCode}</p>
              {!school.latitude && !school.longitude && (
                <span className="inline-block mt-1 px-2 py-1 bg-yellow-100 text-yellow-800 text-xs rounded">
                  Geocoding pending
                </span>
              )}
            </li>
          ))}
        </ul>
      )}
    </div>
  );
} 