import React, { useState } from 'react';
import { parseCSV } from '@/utils/csvParser';
import { School } from '@/types';

interface CSVUploadProps {
  onDataLoaded: (schools: School[]) => void;
  setLoading: (loading: boolean) => void;
}

export default function CSVUpload({ onDataLoaded, setLoading }: CSVUploadProps) {
  const [isDragging, setIsDragging] = useState(false);
  const [fileName, setFileName] = useState<string | null>(null);
  
  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(true);
  };
  
  const handleDragLeave = () => {
    setIsDragging(false);
  };
  
  const handleDrop = async (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
    
    const files = e.dataTransfer.files;
    if (files && files.length > 0) {
      await processFile(files[0]);
    }
  };
  
  const handleFileChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 0) {
      await processFile(files[0]);
    }
  };
  
  const processFile = async (file: File) => {
    if (file.type !== 'text/csv' && !file.name.endsWith('.csv')) {
      alert('Please upload a CSV file');
      return;
    }
    
    setFileName(file.name);
    setLoading(true);
    
    try {
      const schools = await parseCSV(file);
      onDataLoaded(schools);
    } catch (error) {
      console.error('Error parsing CSV:', error);
      alert('Error parsing CSV file');
    } finally {
      setLoading(false);
    }
  };
  
  return (
    <div className="w-full mb-6">
      <div 
        className={`border-2 border-dashed p-8 rounded-lg text-center cursor-pointer transition-colors ${
          isDragging ? 'border-blue-500 bg-blue-50' : 'border-gray-300 hover:border-gray-400'
        }`}
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
        onClick={() => document.getElementById('file-upload')?.click()}
      >
        <input 
          id="file-upload" 
          type="file" 
          accept=".csv" 
          className="hidden" 
          onChange={handleFileChange} 
        />
        
        {fileName ? (
          <div>
            <p className="text-gray-700 mb-1">File loaded: <span className="font-semibold">{fileName}</span></p>
            <p className="text-sm text-gray-500">Click or drop another file to replace</p>
          </div>
        ) : (
          <div>
            <p className="text-lg font-medium text-gray-700 mb-1">Drag and drop your CSV file here</p>
            <p className="text-sm text-gray-500">or click to select a file</p>
          </div>
        )}
      </div>
      
      <div className="mt-2 text-xs text-gray-500">
        <p>CSV should include: School Name, School District, Address, City, State, Zip Code</p>
      </div>
    </div>
  );
} 