'use client';

import { useState, useEffect } from 'react';
import dynamic from 'next/dynamic';
import { School, MapViewport } from '@/types';
import CSVUpload from '@/components/CSVUpload';
import SchoolList from '@/components/SchoolList';
import { geocodeSchools } from '@/utils/geocoding';

// Import Map component dynamically to avoid SSR issues with Leaflet
const Map = dynamic(() => import('@/components/Map'), {
  ssr: false,
  loading: () => (
    <div className="h-full w-full flex items-center justify-center bg-gray-100">
      <p>Loading map...</p>
    </div>
  ),
});

export default function Home() {
  const [schools, setSchools] = useState<School[]>([]);
  const [loading, setLoading] = useState(false);
  const [geocoding, setGeocoding] = useState(false);
  const [selectedSchool, setSelectedSchool] = useState<School | null>(null);
  const [viewport, setViewport] = useState<MapViewport>({
    center: [39.8283, -98.5795], // Center of the United States
    zoom: 4,
  });

  // Handle CSV data after import
  const handleDataLoaded = async (importedSchools: School[]) => {
    setSchools(importedSchools);
    setGeocoding(true);
    
    // Geocode the imported schools
    const geocodedSchools = await geocodeSchools(importedSchools);
    setSchools(geocodedSchools);
    setGeocoding(false);
    
    // Update viewport if we have schools with coordinates
    const schoolsWithCoords = geocodedSchools.filter(
      (school) => school.latitude && school.longitude
    );
    
    if (schoolsWithCoords.length > 0) {
      // Center on the first school with coordinates
      setViewport({
        center: [schoolsWithCoords[0].latitude!, schoolsWithCoords[0].longitude!],
        zoom: 10,
      });
    }
  };
  
  // Update viewport when a school is selected
  useEffect(() => {
    if (selectedSchool?.latitude && selectedSchool?.longitude) {
      setViewport({
        center: [selectedSchool.latitude, selectedSchool.longitude],
        zoom: 14,
      });
    }
  }, [selectedSchool]);

  return (
    <main className="flex min-h-screen flex-col px-4 py-6 md:px-8 md:py-10">
      <h1 className="text-3xl font-bold mb-6">School Photography Client Map</h1>
      
      <div className="mb-6">
        <CSVUpload onDataLoaded={handleDataLoaded} setLoading={setLoading} />
      </div>
      
      {(loading || geocoding) && (
        <div className="fixed inset-0 bg-black bg-opacity-20 flex items-center justify-center z-50">
          <div className="bg-white p-6 rounded-lg shadow-lg">
            <p className="font-medium">{geocoding ? 'Geocoding addresses...' : 'Processing data...'}</p>
            <p className="text-sm text-gray-500 mt-1">This may take a moment</p>
          </div>
        </div>
      )}
      
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 flex-1 h-[70vh]">
        <div className="md:col-span-1 h-full overflow-hidden">
          <SchoolList 
            schools={schools} 
            selectedSchool={selectedSchool} 
            setSelectedSchool={setSelectedSchool} 
          />
        </div>
        
        <div className="md:col-span-2 h-full rounded-lg overflow-hidden shadow-lg">
          <Map schools={schools} viewport={viewport} />
        </div>
      </div>
    </main>
  );
}
